

body {
  font-family: 'Cardo', serif;
}

.App-title
{
  color: #482d0a;
  font-size: 3rem;
  margin-bottom: 1rem;
  font-family: 'Pathway Gothic One', sans-serif;
  text-align: center;
}

h2
{
  color: #482d0a;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 3rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
  margin-top: 0;
}

p
{
  font-size: 1.2rem;
  color: #482d0a;
  padding-left: 15px;
  padding-right: 15px;
}

.content
{
  background-color: #f1eac3;
  text-align: center;
  padding-top: 50px;
  padding-bottom: 50px;
  border-top: 10px solid #c9953d;
  border-bottom: 10px solid #c9953d;
}

.section-background
{
  background-size: contain;
  width: 100%;
  height: 378px;
}

.iframe-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 35px;
    height: 0;
    overflow: hidden;
}

.iframe-container iframe {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media only screen and (min-width : 992px) {
  .section-background
  {
    background-size: cover;
    width: 100%;
    height: 532px;
  }
}

@media only screen and (min-width : 1200px) {
  .section-background
  {
    background-size: cover;
    width: 100%;
    height: 735px;
  }
}

.center-all
{
  position: relative;
  width: 100%;
}

.center-all-children
{
  width: 100%;
}

@media only screen and (min-width : 992px) {
  .center-all-children
  {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 65%;
  }
}

.app-subtitle
{
  color: #482d0a;
  font-size: 1.5rem;
  margin-top: 0;
  text-align: center;
}

@media only screen and (min-width : 992px) {
  .App-title
  {
    color: #482d0a;
    font-size: 7rem;
    margin-bottom: 1rem;
    font-family: 'Pathway Gothic One', sans-serif;
    text-align: center;
  }
  .app-subtitle
  {
    color: #482d0a;
    font-size: 3rem;
    margin-top: 0;
    text-align: center;
  }
  h2
  {
    color: #482d0a;
    font-family: 'Pathway Gothic One', sans-serif;
    font-size: 4rem;
    text-transform: uppercase;
    margin-bottom: 1rem;
    margin-top: 0;
  }
}

@import './components/welcome/welcome.css';
@import './components/year_one/YearOne.css';
@import './components/year_two/YearTwo.css';
@import './components/year_three/YearThree.css';
@import './components/year_four/YearFour.css';
@import './components/year_five/YearFive.css';


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.ipl-progress-indicator.available {
  opacity: 0;
}

.ipl-progress-indicator 
{
  background-color: #f5f5f5;
  width: 100%;
  height: 100%;
  position: fixed;
  opacity: 1;
  pointer-events: none;
  -webkit-transition: opacity cubic-bezier(.4, 0, .2, 1) 436ms;
  -o-transition: opacity cubic-bezier(.4, 0, .2, 1) 436ms;
  transition: opacity cubic-bezier(.4, 0, .2, 1) 436ms;
  z-index: 9999;
}

.insp-logo-frame 
{
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -moz-flex-direction: column;
  -ms-flex-direction: column;
      flex-direction: column;
  -moz-justify-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-animation: fadein 436ms;
  animation: fadein 436ms;
  height: 98%;
}

.insp-logo-frame-img
{
  width: 500px;
  height: 110px;
  -ms-flex-item-align: center;
      align-self: center;
}

.ipl-progress-indicator-head 
{
  background-color: #c6dafc;
  height: 4px;
  overflow: hidden;
  position: relative;
}

.ipl-progress-indicator .first-indicator, .ipl-progress-indicator .second-indicator 
{
  background-color: #056D8B;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: absolute;
  -webkit-transform-origin: left center;
  -ms-transform-origin: left center;
      transform-origin: left center;
  -webkit-transform: scaleX(0);
  -ms-transform: scaleX(0);
      transform: scaleX(0);
}

.ipl-progress-indicator .first-indicator 
{
  -webkit-animation: first-indicator 2000ms linear infinite;
  animation: first-indicator 2000ms linear infinite;
}

.ipl-progress-indicator .second-indicator 
{
  -webkit-animation: second-indicator 2000ms linear infinite;
  animation: second-indicator 2000ms linear infinite;
}

.ipl-progress-indicator .insp-logo 
{
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  border-radius: 50%;
  -ms-flex-item-align: center;
      align-self: center;
}

@keyframes App-logo-spin 
{
  from 
  {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to 
  {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes fadein 
{
  from 
  {
    opacity: 0;
  }
  to 
  {
    opacity: 1;
  }
}

@keyframes fadein 
{
  from 
  {
    opacity: 0;
  }
  to 
  {
    opacity: 1;
  }
}

@-webkit-keyframes first-indicator 
{
  0% 
  {
  -webkit-transform: translate(0%) scaleX(0);
          transform: translate(0%) scaleX(0);
  }
  25% 
  {
  -webkit-transform: translate(0%) scaleX(0.5);
          transform: translate(0%) scaleX(0.5);
  }
  50% 
  {
  -webkit-transform: translate(25%) scaleX(0.75);
          transform: translate(25%) scaleX(0.75);
  }
  75% 
  {
  -webkit-transform: translate(100%) scaleX(0);
          transform: translate(100%) scaleX(0);
  }
  100% 
  {
    -webkit-transform: translate(100%) scaleX(0);
            transform: translate(100%) scaleX(0);
  }
}

@keyframes first-indicator 
{
  0% 
  {
  -webkit-transform: translate(0%) scaleX(0);
          transform: translate(0%) scaleX(0);
  }
  25% 
  {
  -webkit-transform: translate(0%) scaleX(0.5);
          transform: translate(0%) scaleX(0.5);
  }
  50% 
  {
  -webkit-transform: translate(25%) scaleX(0.75);
          transform: translate(25%) scaleX(0.75);
  }
  75% 
  {
  -webkit-transform: translate(100%) scaleX(0);
          transform: translate(100%) scaleX(0);
  }
  100% 
  {
    -webkit-transform: translate(100%) scaleX(0);
            transform: translate(100%) scaleX(0);
  }
}

@-webkit-keyframes second-indicator
{
  0% 
  {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  60% 
  {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  80% 
  {
    -webkit-transform: translate(0%) scaleX(0.6);
            transform: translate(0%) scaleX(0.6);
  }
  100% 
  {
    -webkit-transform: translate(100%) scaleX(0.1);
            transform: translate(100%) scaleX(0.1);
  }
}

@keyframes second-indicator
{
  0% 
  {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  60% 
  {
    -webkit-transform: translate(0%) scaleX(0);
            transform: translate(0%) scaleX(0);
  }
  80% 
  {
    -webkit-transform: translate(0%) scaleX(0.6);
            transform: translate(0%) scaleX(0.6);
  }
  100% 
  {
    -webkit-transform: translate(100%) scaleX(0.1);
            transform: translate(100%) scaleX(0.1);
  }
}

.snitch {
  background-image: url('http://fc03.deviantart.net/fs23/f/2007/352/4/c/HP___GOLDEN_SNITCH_by_Aoeiya.gif');
  margin: 0 auto;
  height: 125px;
  opacity: 1;
  width: 150px;
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}



